import React from "react";
import "./App.css";
import { RecoilRoot } from "recoil";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import "./index.css";
import Root from "./Pages/Main/root";
import ErrorPage from "./Pages/Main/error-page";
import { Webpages, websiteRoot } from "./Utilites/Globals";
import clsx from "clsx";
//url rewrite
//https://www.youtube.com/watch?v=hkEFPzixiVE

const defaultLoader = () => {
  return {};
};

const router = createBrowserRouter(
  [
    {
      path: websiteRoot,
      element: <Root />,
      errorElement: <ErrorPage />,
      loader: defaultLoader,
      children: Webpages.map((page) => ({
        path: page.path,
        element: page.element,
        loader: defaultLoader,
      })),
    },
  ],
  {
    // baseurl?
    // basename:'/build'
  }
);

function App() {
  return (
    <div className="App flex flex-col overflow-hidden">
      <RecoilRoot>
        {/* <StartingMenu /> */}
        <RouterProvider router={router} />
      </RecoilRoot>
    </div>
  );
}

export default App;
