import React, { useMemo } from "react";
import { Outlet, matchPath } from "react-router";
import { useState, useEffect } from "react";
import { Webpages, websiteTitle } from "../../Utilites/Globals";
import { BiUserCircle, BiArrowFromLeft, BiX } from "react-icons/bi";
import { useRecoilState } from "recoil";
import { CurrentUser, ReactUserAccount } from "../../Utilites/Atoms";
import UserProfileSlideover from "../SlideOvers/UserProfileSlideover";
import clsx from "clsx";
import uuid from "react-uuid";
import localforage from "localforage";
import { UserAccount } from "../../Utilites/Decoders";
import RouterUtility from "../../Utilites/RouterUtils";

const Root = () => {
  const [currentReactAccount, setCurrentReactAccount] =
    useRecoilState(ReactUserAccount);
  const [openUserProfile, setOpenUserProfile] = useState<boolean>(false);
  const [openMenu, setOpenMenu] = useState<boolean>(false);
  const routerUtility = new RouterUtility();


  const loadUser = async () => {
    const act: UserAccount = await routerUtility.getLocal(
      "currentReactAccount",
      false
    );

    if (act && act.FIRST_NAME !== "Guest") {
      setCurrentReactAccount(act);
      const lastPage = await routerUtility
        .getLocal("lastPage", false)
        .then((e) => (e !== null ? e : "/"));

      if ((routerUtility.location.pathname as string) === "/") {
        routerUtility.navigateTo(lastPage, null, { replace: true });
      }
    } else {
      const pathstr = routerUtility.location.pathname as string;
      console.log("clearing localstorage");
      localforage.clear();
      let page = Webpages.find((x) => x.path === pathstr);
      if ((page && page.permissionLevel > 0) || page?.mode === "Inactive") {
        routerUtility.navigateTo("/Home");
      }
    }
  };

  useEffect(() => {
    loadUser();
  }, []);

  //if you are not on the index page, log the page as your last visted page
  useEffect(() => {
    if ((routerUtility.location.pathname as string) !== "/") {
      const strLoc = routerUtility.location.pathname as string;
      routerUtility.setCache(undefined, { name: "lastPage", data: strLoc });
      // localforage.setItem('lastPage', routerUtility.location.pathname);
    }
  }, [routerUtility.location]);

  const availWebPages = useMemo(
    () =>
      currentReactAccount.FIRST_NAME === "Guest"
        ? Webpages.filter(
            (page) => page.active && page.ID >= 0 && page.permissionLevel === 0
          )
        : Webpages.filter(
            (page) =>
              page.active &&
              page.ID >= 0 &&
              page.permissionLevel <= 0 + currentReactAccount.PERMISSIONS
          ),
    [currentReactAccount]
  );

  const menuHeader = useMemo(() =>{
    const found = Webpages.find((page) => matchPath(page.path, routerUtility.location.pathname) !== null)
    if(found){
      return found.mode;
    }else{
      return "Open Menu"
    }
    }, [routerUtility.location]
  );

  return (
    <div className="flex flex-col overflow-hidden h-screen w-screen">
      {/* user profile init */}
      <UserProfileSlideover
        isOpen={openUserProfile}
        onClose={() => setOpenUserProfile(false)}
        onSubmit={() => console.log("profile submitted: ")}
        currentReactAccount={currentReactAccount}
      />

      {/* title bar*/}
      <header className="flex flex-row bg-gray-800 justify-between w-full px-2 border-b">
        {/* left side */}
        <div className="flex flex-col w-auto my-1">
          <p className="flex flex-row text-white font-bold">
            {websiteTitle}
          </p>
          <button
            className="text-white hover:text-sky-500 space-x-1 flex flex-row items-center"
            onClick={() => setOpenMenu(!openMenu)}
          >
            <p>
              {menuHeader}
            </p>
            {!openMenu ? <BiArrowFromLeft size={20} /> : <BiX size={20} />}
          </button>
        </div>

        {/*right side*/}
        <div
          className="flex flex-col w-auto my-1 cursor-pointer"
          onClick={() => setOpenUserProfile(true)}
        >
          <div className="flex flex-row items-center justify-end">
            <p
              className={clsx(
                "",
                !currentReactAccount
                  ? "text-red-500"
                  : "text-green-500 capitalize"
              )}
            >
              {currentReactAccount
                ? `${currentReactAccount.FIRST_NAME} ${currentReactAccount.LAST_NAME}`
                : "SIGN IN HERE"}
            </p>
            <BiUserCircle
              size={20}
              className={clsx(
                "flex flex-col ml-2",
                !currentReactAccount ? "text-red-500" : "text-green-500"
              )}
            />
          </div>
          <div className="flex flex-row">
            <p className="text-white text-right">
              {new Date().toLocaleDateString()}
            </p>
            <p className="text-left text-green-500">
              {new Date().toLocaleTimeString()}
            </p>
          </div>
        </div>
      </header>

      {/*main content*/}
      <div className="flex flex-row w-full h-full overflow-hidden">
        {/* side bar */}
        {openMenu ? (
          <nav className="flex flex-col h-full bg-slate-800 shadow-md divide-y">
            <ul className="w-full truncate overflow-x-hidden">
              {availWebPages.map((page) => (
                <li key={`${uuid()}`}>
                  <a href={page.path}>
                    <p
                      className={clsx(
                        "px-4 text-white hover:text-green-500 text-left truncate"
                      )}
                    >
                      {page.mode}
                    </p>
                  </a>
                </li>
              ))}
            </ul>
          </nav>
        ) : (
          <></>
        )}

        {/* content */}
        <div className={"flex flex-col w-full h-full bg-slate-400"}>
          <Outlet />
        </div>
      </div>
    </div>
  );
};

export default Root;
